import axios from 'axios';
Vue.use(Vuex);
 
export default new Vuex.Store({
  state: 
  {
   
    Turno: 
    {
      Turno: 'SIN TURNO',
      TipoIdentificacion: '',
      NumeroIdentificacion: '',
      Nombre: '',
      Categoria: '',
      Edad: '',
      TipoEdad: '',
      Prioridad: '',
      ValidarExisteTurno: '',
      YaExisteTurnoHoy: '',
      Turno: '',
      FichoTurno: '',
      Error: '',
      MensajeError: ''
    },
    ConfigTurnero: 
    {
      CodigoTurnero: '',
      NombreTurnero: '',	
      TieneLectorCedula: '',
      TieneIngresoManual: '',
      TieneCategorias: '',
      TienePrioridad: '',
      ValidarExisteTurno: '',
      MensajeBienvenida: '<b>Bienvenido</b><br> Cuidamos la vida a cada instante',
      MensajeLector: 'SIN MENSAJE <br> DE LECTOR',
      MensajeIngresoManual: 'SIN MENSAJE <br> DE INGRESO MANUAL',
      MensajeTiposDocumento: 'SIN MENSAJE TIPO DOCUMENTO',
      MensajeDatosPersonales: 'SIN MENSAJE DATOS PERSONALES',
      MensajeCategorias: 'SIN MENSAJE CATEGORIAS',
      MensajeTurnoGenerado: '',
      MensajeSinTipoDocumento: 'SIN MENSAJE SIN TIPO DOCUMENTO',
      MensajeSinNumeroDocumento: 'SIN MENSAJE SIN NUMERO DOCUMENTO',
      MensajeSinNombre: 'SIN MENSAJE SIN NOMBRE',
      MensajeSinEdad: 'SIN MENSAJE SIN EDAD',
      MensajeSinTipoEdad: '',
      MensajeSinCategoria: 'SIN MENSAJE SIN CATEGORIA',	
      //TiposDocumento: Array(),
      //Categorias: Array(),
      //Prioridades: Array(),
      Error: '',
      MensajeError: '',
      TiposDocumento: 
      [
       { Nombre: 'TIPO DOC1',Codigo: '006',Color:'primary',Clase:'m-boton text-h6 headline'},
       { Nombre: 'TIPO DOC2',Codigo: '003',Color:'primary',Clase:'m-boton text-h6 headline'},
       { Nombre: 'TIPO DOC3',Codigo: '01',Color:'primary',Clase:'m-boton text-h6 headline'},
       { Nombre: 'TIPO DOC4',Codigo: '002',Color:'primary',Clase:'m-boton text-h6 headline'},
       { Nombre: 'TIPO DOC5',Codigo: '005',Color:'primary',Clase:'m-boton text-h6 headline'},
       { Nombre: 'TIPO DOC6',Codigo: '007',Color:'primary',Clase:'m-boton text-h6 headline'},      
      ],
      Categorias: 
      [
       { Nombre: 'CAT1',Codigo: '006',Color:'primary',Clase:'m-boton text-h6 headline'},
       { Nombre: 'CAT2',Codigo: '003',Color:'primary',Clase:'m-boton text-h6 headline'},
       { Nombre: 'CAT3',Codigo: '01',Color:'primary',Clase:'m-boton text-h6 headline'},
       { Nombre: 'CAT4',Codigo: '002',Color:'primary',Clase:'m-boton text-h6 headline'},
       { Nombre: 'CAT5',Codigo: '005',Color:'primary',Clase:'m-boton text-h6 headline'},
       { Nombre: 'CAT6',Codigo: '007',Color:'primary',Clase:'m-boton text-h6 headline'},      
      ],
      Prioridades: 
      [
       { Nombre: 'P1',Codigo: '006',Color:'white',EsPrioridad:false,Icono: 'mdi-human-pregnant'},
       { Nombre: 'P2',Codigo: '003',Color:'white',EsPrioridad:true,Icono: 'mdi-human-pregnant'},
       { Nombre: 'P3',Codigo: '01',Color:'white',EsPrioridad:true,Icono: 'mdi-human-cane'},
       { Nombre: 'P4',Codigo: '002',Color:'white',EsPrioridad:true,Icono: 'mdi-human-wheelchair'},
       { Nombre: 'P5',Codigo: '005',Color:'white',EsPrioridad:true,Icono: 'mdi-human-male-child'},
      ],
    },
    Prueba: 'Prueba vuex'
  },
  getters: {},
  mutations: {


    
    setConfigTurnero (state, payload) 
    {
      //console.log("setConfigTurnero");
      //console.log(payload);      
      state.ConfigTurnero = payload
    },

    changePrueba (state, payload) 
    {
      state.Prueba = payload
    },

    seleccionarCategoria (state, payload) 
    {

      //console.log("inicio");
      for (var i = 0; i < state.ConfigTurnero.Categorias.length; i+=1) 
      {
        console.log(state.ConfigTurnero.Categorias[i].Codigo + "-" +  payload.Codigo);
        if (state.ConfigTurnero.Categorias[i].Codigo ==  payload.Codigo)
        {
          state.ConfigTurnero.Categorias[i].Color = "white";
          state.ConfigTurnero.Categorias[i].Clase = "m-botonSeleccionado text-h6 headline pt-4";
          state.Turno.Categoria = payload.Codigo;
        }
        else
        {
          state.ConfigTurnero.Categorias[i].Clase = "m-boton text-h6 headline pt-4";
          state.ConfigTurnero.Categorias[i].Color = "primary";
        }
      }

    },

    seleccionarTipoDocumento (state, payload) 
    {

      //console.log("inicio");
      for (var i = 0; i < state.ConfigTurnero.TiposDocumento.length; i+=1) 
      {
        console.log(state.ConfigTurnero.TiposDocumento[i].Codigo + "-" +  payload.Codigo);
        if (state.ConfigTurnero.TiposDocumento[i].Codigo ==  payload.Codigo)
        {
          state.ConfigTurnero.TiposDocumento[i].Color = "white";
          state.ConfigTurnero.TiposDocumento[i].Clase = "m-botonSeleccionado text-h6 headline pt-4";
          state.Turno.TipoIdentificacion = payload.Codigo;
        }
        else
        {
          state.ConfigTurnero.TiposDocumento[i].Clase = "m-boton text-h6 headline pt-4";
          state.ConfigTurnero.TiposDocumento[i].Color = "primary";
        }
      }

    }, 

    seleccionarPrioridad (state, payload) 
    {

      //console.log("inicio");
      for (var i = 0; i < state.ConfigTurnero.Prioridades.length; i+=1) 
      {
        console.log(state.ConfigTurnero.Prioridades[i].Codigo + "-" +  payload.Codigo);
        if (state.ConfigTurnero.Prioridades[i].Codigo ==  payload.Codigo)
        {
          console.log("codigo encontrado");
          state.ConfigTurnero.Prioridades[i].Color = "primary";
          state.Turno.Prioridad = payload.Codigo;
          console.log("Prioridad:" + state.Turno.Prioridad);          
          console.log("Prioridad turno:" + state.Turno.Prioridad);
          console.log(state.Turno);
        }
        else
        {
          state.ConfigTurnero.Prioridades[i].Color = 'white';
        }
      }

    }, 


    changeTurno (state, payload) 
    {
      state.Turno = payload
    },

    changeTurnoTurno (state, payload) 
    {
      state.Turno.Turno = payload
    },

    changeTurnoTipoIdentificacion (state, payload) 
    {
      state.Turno.TipoIdentificacion = payload
    },

    changeTurnoNumeroIdentificacion (state, payload) 
    {
      state.Turno.NumeroIdentificacion = payload
    },

    changeTurnoNombre (state, payload) 
    {
      state.Turno.Nombre = payload
    },

    changeTurnoCategoria (state, payload) 
    {
      state.Turno.Categoria = payload
    },

    changeTurnoEdad (state, payload) 
    {
      state.Turno.Edad = payload
    },

    changeTurnoTipoEdad (state, payload) 
    {
      state.Turno.TipoEdad = payload
    },

    changeTurnoPrioridad (state, payload) 
    {
      state.Turno.Prioridad = payload
    },

    inicializarTurno (state, payload) 
    {
      state.Turno.Prioridad = '';
      state.Turno.Edad = '';
      state.Turno.Categoria  = '';
      state.Turno.TipoIdentificacion  = '';  
      state.Turno.NumeroIdentificacion  = '';  
      state.Turno.Turno  = 'SIN TURNO';  

      state.Turno.Nombre  = '';  
      for (var i = 0; i < state.ConfigTurnero.Prioridades.length; i+=1) 
      {
          state.ConfigTurnero.Prioridades[i].Color = 'white';
      }
      for (var i = 0; i < state.ConfigTurnero.TiposDocumento.length; i+=1) 
      {
          state.ConfigTurnero.TiposDocumento[i].Color = 'primary';
          state.ConfigTurnero.TiposDocumento[i].Clase = "m-boton text-h6 headline pt-4";

      }
      for (var i = 0; i < state.ConfigTurnero.Categorias.length; i+=1) 
      {

          state.ConfigTurnero.Categorias[i].Color = 'primary';
          state.ConfigTurnero.Categorias[i].Clase = "m-boton text-h6 headline pt-4";          
      }

        
    },


  },
  actions: 
  {


    changeTurno (context, payload) 
    {
      setTimeout(() => 
      {
        context.commit("changeTurno", payload);
      }, 2000);
    },
    changePrueba (context, payload) 
    {
      setTimeout(() => 
      {
        context.commit("changePrueba", payload);
      }, 2000);
    },

    inicializarTurnero({ commit }) 
    {

      // axios.get('http://10.17.2.33/matrix/webapi/turnero/obtenerconfiguracionturnero.php?wemp_pmla=01&codigoTurnero=09')
      // .then(response => {commit('setConfigTurnero', response.data)})
      axios.get('../obtenerconfiguracionturnero.php?wemp_pmla=01&codigoTurnero=09')
      .then(response => {commit('setConfigTurnero', response.data)})

    }

  }
});