          <v-card light color="withe" height="800"  elevation="0">          

          <router-view class="ma-0 pa-0" ></router-view>
          </v-card>
        </v-col>
      </v-row>      

      <v-bottom-navigation fixed  height="100px" dark background-color="primary">
        <v-row justify="end">
          <v-btn  value="recent">
            <v-img  src="@/assets/logoaunatrans.png"  width="150px"  v-on:click="IrHome()"></v-img>
          </v-btn>
        </v-row>
      </v-bottom-navigation>
    </v-main>


  </v-app>
</template>


<style>

@font-face {
  font-family: "Kastelov";
  src: local("Kastelov"),
   url(fonts/Kastelov-AxiformaBold.otf) format("truetype");
}

@font-face {
  font-family: "Axiforma";
  src: local("Axiforma"),
   url(fonts/Axiforma-Regular.ttf) format("truetype");
}

/* make the video stretch to fill the screen in WebKit */
:-webkit-full-screen #bodyPrincipal {
  width: 		100%;
  height: 	100%;
}

:fullscreen
:-ms-fullscreen,
:-webkit-full-screen,
:-moz-full-screen {
    overflow: auto !important;
}

/* Al poner el curso encima (hover) */

.btn:hover {  background: #099fc4;  background-image: -webkit-linear-gradient(top, #2079b0, lightgray);
  background-image: -moz-linear-gradient(top, #099fc4, lightgray);
  background-image: -ms-linear-gradient(top, #099fc4, lightgray);
  background-image: -o-linear-gradient(top, #099fc4, lightgray);
  background-image: linear-gradient(to bottom, #099fc4, lightgray);  text-decoration: none;
}

</style>
<script>

  export default 
  {
    data: () => ({ drawer: null }),
    computed: 
    {
       
      ConfigTurnero () 
      {
        return this.$store.state.ConfigTurnero;
      }
    },  

    mounted: function() 
    {
      //alert('prueba');
      console.log('Bienvenida');
      this.$store.dispatch("inicializarTurnero");  
      document.addEventListener("click", function(e) 
      {			
        var videoElement = document.getElementById("AppAtrilTurnero");
        if (!document.mozFullScreen && !document.webkitFullScreen) 
        {
          if (videoElement.mozRequestFullScreen) 
          {
            videoElement.mozRequestFullScreen();
          } 
          else 
          {
            videoElement.webkitRequestFullScreen(Element.ALLOW_KEYBOARD_INPUT);
        }
        }
      }, false);
      
      
    },

    updated: function() 
    {
      //alert('updated');
      var videoElement = document.getElementById("AppAtrilTurnero");
        if (!document.mozFullScreen && !document.webkitFullScreen) 
        {
          if (videoElement.mozRequestFullScreen) 
          {
            videoElement.mozRequestFullScreen();
          } 
          else 
          {
            videoElement.webkitRequestFullScreen(Element.ALLOW_KEYBOARD_INPUT);
          }
        }   
      
    },
    methods: 
    {    
      redirect(parameter) 
      {
        this.$router.push(parameter);
      },
      IrHome() 
      {
  
          this.redirect(`/`);
      
      }
    },


  }
</script>

